    /**
     */

    question_contains_event: function(){
      let question = this.staging.current_question.text.toLowerCase();
      for (const key in this.staging.actions){
        const action = this.staging.actions[key];
        if (question.includes(action.text)){
          return true
        }
      }
      return false
    },

    is_question_suspicious: function(){
      let trimmedQuestion = this.staging.current_question.text.toLowerCase();
      for (const key in this.staging.actions){
        const action = this.staging.actions[key];
        if (trimmedQuestion.includes(action.text.toLowerCase())){
          return false
        }
      }

      // must not include the following words
      for (let id in bad_question_keywords){
        const keyword = bad_question_keywords[id];
        if (trimmedQuestion.includes(keyword)){
          return true;
        }
      }

      // must not be one of the default questions
      for (let id in default_questions){
        const keyword = default_questions[id];
        if (trimmedQuestion.includes(keyword)){
          return true;
        }
      }

      // must ends with ?
      // if(!trimmedQuestion.endsWith('?'))
      //   return true;

      // must include one or more of the following words
      for (let id in question_keywords){
        const keyword = question_keywords[id];
        if (trimmedQuestion.includes(keyword)){
          return false;
        }
      }

      return true;

    },

    on_question_text_change: function(){
      if(this.is_question_suspicious()){
       this.show_qa_error("This question POSSIBLY doesn't include a highlighted event. You are allowed to use a different form of the selected action word (e.g. teach v.s. taught) or use an imaginary but concrete action word for [Sub-event] question. You can still add this question if you are sure.");
      }else{
        this.hide_qa_error()
      }
    },

